<template>
  <div class="add-goods">
    <div class="step">
      <a-steps :current="current" class="step-bar">
        <a-step v-for="item in steps" :key="item.title" :title="item.title" />
      </a-steps>
      <div class="steps-content">
        <base-detail
          v-if="current === 0"
          ref="baseSubmit"
          @changeCur="changeCur"
          :form="form"
        />
        <sale-detail v-else-if="current === 1" :form="form" :id='id' ref="saleSubmit"/>

        <div class="steps-action">
          <a-button
            v-if="current < steps.length - 1"
            type="primary"
            @click="next"
          >
            下一步
          </a-button>
          <a-button
            v-if="current == steps.length - 1"
            type="primary"
            @click="successed"
          >
            完成
          </a-button>
          <a-button v-if="current > 0" style="margin-left: 8px" @click="prev">
            上一步
          </a-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import BaseDetail from '@/components/BaseDetail.vue';
import SaleDetail from '@/components/SaleDetail.vue';
import api from '@/api/goods';

export default {
  data() {
    return {
      current: 0,
      id: '',
      form: {
        title: '',
        desc: '',
        category: '',
        tags: '',
        price: 1,
        price_off: '',
        inventory: 1,
        unit: '',
        images: [],
      },
      steps: [
        {
          title: '商品基本信息',
        },
        {
          title: '商品销售信息',
        },
      ],
    };
  },
  components: {
    BaseDetail,
    SaleDetail,
  },
  methods: {
    next() {
      this.$refs.baseSubmit.onSubmit();
    },
    changeCur() {
      this.current += 1;
    },
    prev() {
      this.current -= 1;
    },
    successed() {
      this.$refs.saleSubmit.onSubmit();
    },
  },
  created() {
    this.id = this.$route.params.id;
    if (this.id) {
      api.detail(this.id).then((res) => {
        this.form = res;
      });
    }
  },
};
</script>
<style lang="less" scoped>
.step-bar {
  width: 50%;
  margin: 10px auto;
}
.steps-content {
  margin-top: 20px;
  border: 1px dashed #e9e9e9;
  border-radius: 6px;
  background-color: #fafafa;
  min-height: 450px;
  text-align: center;
  position: relative;
  .steps-action {
    margin-top: 10px;
    position: absolute;
    bottom: 10px;
    left: 0;
    right: 0;
  }
}
</style>
