<template>
  <a-form-model
    ref="ruleForm"
    :model="form"
    :rules="rules"
    :label-col="labelCol"
    :wrapper-col="wrapperCol"
  >
    <a-form-model-item label="副标题" required prop="title">
      <a-input v-model="form.title" />
    </a-form-model-item>
    <a-form-model-item label="商品描述"  prop="desc">
      <a-input v-model="form.desc" />
    </a-form-model-item>
    <a-form-model-item label="商品类目" required prop="category">
      <a-select v-model="form.category" placeholder="请选择类目" @change="changeCate">
        <a-select-option
          v-for="c in categoryList"
          :key="c.id"
          :value="c.id"

        >
          {{ c.name }}
        </a-select-option>
      </a-select>
      <a-select v-model="form.c_item" placeholder="请选择子类目">
        <a-select-option
          v-for="item in c_items"
          :key="item"
          :value="item"
        >
          {{ item }}
        </a-select-option>
      </a-select>
    </a-form-model-item>
    <a-form-model-item label="商品标签" required prop="tags">
      <a-select
        mode="tags"
        placeholder="请选择"
        :default-value="['包邮次日达']"
        v-model="form.tags"
      >
        <a-select-option value="包邮，次日达">
          包邮，次日达
        </a-select-option>
      </a-select>
    </a-form-model-item>
  </a-form-model>
</template>
<script>
import api from '@/api/category';

export default {
  data() {
    return {
      labelCol: { span: 4 },
      wrapperCol: { span: 14 },
      categoryList: [],
      c_items: [],
      rules: {},
    };
  },
  props: ['form'],
  created() {
    api.getCategory().then((res) => {
      this.categoryList = res.data;
    });
  },
  methods: {
    onSubmit() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.$emit('changeCur', this.form);
          return true;
        }
        console.log('error submit!!');
        return false;
      });
    },
    resetForm() {
      this.$refs.ruleForm.resetFields();
    },
    changeCate(c) {
      this.c_items = this.categoryList.filter((item) => item.id === c)[0].c_items;
    },
  },
};
</script>
